@import '../../../../../../../../../helpers/_variables';

.artist-listeners {
  z-index: 1;
  text-align: right;

  width: 20%;
  padding: $padding;

  color: $grey;
  font-weight: 100;
  font-size: $medium;
  letter-spacing: 1px;

  @media screen and ( max-width: 522px) {
    text-align: center;

    width: 100%; }

  &__label {
    font-weight: 300;
    text-transform: uppercase;
    font-size: $x-small * 1.1;
    letter-spacing: 1px; } }
