// Colors

$green               : rgb(30, 215, 96);
$light-black         : rgb(40, 40, 40);
$medium-light-black  : rgb(55, 55, 55);
$black               : rgb(24, 24, 24);
$grey                : rgb(170, 170, 170);
$sand                : rgb(200, 200, 200);
$white               : rgb(255, 255, 255);
$blue                : #4688d7;

// Spacing

$padding-xxs  : 3px;
$padding-xs   : 5px;
$padding-sm   : 10px;
$padding      : 15px;
$padding-lg   : 30px;
$padding-xl   : 60px;

$margin-xs  : 5px;
$margin-sm  : 10px;
$margin     : 15px;
$margin-lg  : 30px;
$margin-xl  : 60px;

// Borders

$border-radius-xxs   : 2px;
$border-radius-xs    : 4px;
$border-radius       : 15px;
$border-radius-lg    : 20px;
$border-radius-circle: 50%;

// Font-sizes

$x-small    : 11px;
$small      : 14px;
$medium     : 16px;
$large      : 20px;
$x-large    : 24px;
$xx-large   : 36px;
$xxx-large  : 52px;

// Durations
$hover-transition-duration  : 400ms;
$highlighter-duration       : 400ms;
